import {React, useEffect, useState} from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Routes, Link, Navigate } from "react-router-dom";

import Home from "../home";
import Cofounder from "../pages/cofounder";
import Investor from "../pages/invertor";
import Login from "../logs/login";
import Signup from "../logs/signup";

import "./nav.style.scss";

import logo from "../ximg/logo.svg";
import web from "../ximg/web.svg";

import send from "./navimages/send.svg";
import clip from "./navimages/clip.svg";

function showProfileBox() {
    ReactDOM.render(null, document.getElementById("search_id_profile"));
    document.getElementById("search_id_profile").style.transform = "";
}

function logout() {
    localStorage.removeItem("token_access");
    window.location.reload();
}

function showSearchedProfile(su) {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };
    fetch("https://api.coihub.co/api/accounts/profile/" + su + "/", requestOptions)
        .then(response => response.json())
        .then((res) => {
            document.getElementById("search_id_profile").style.transform = "scaleX(100%) scaleY(100%)";
            if(res.is_admin == true) {
                ReactDOM.render(<div className="profile_box"><button className="close_profile" onClick={() => showProfileBox()}>close</button><div className="profile_show" style={{ backgroundImage: "url(https://api.coihub.co" + res.cover_image + ")" }}><img src={"https://api.coihub.co" + res.profile_image} title={"profile image of " + res.first_name} /></div><p><b>Full Name: </b> {res.id==localStorage.getItem("this_user_id") ? "you" : res.first_name}</p><p><b>Mobile: </b> {res.mobile}</p><p><b>Location: </b> {res.location}</p><div style={{padding: "10px 20px", backgroundColor: "#a3dd83", margin: "10px 0px", borderRadius: "50px", width: "fit-content"}}>Admin</div></div>, document.getElementById("search_id_profile"))
            } else {
                ReactDOM.render(<div className="profile_box"><button className="close_profile" onClick={() => showProfileBox()}>close</button><div className="profile_show" style={{ backgroundImage: "url(https://api.coihub.co" + res.cover_image + ")" }}><img src={"https://api.coihub.co" + res.profile_image} title={"profile image of " + res.first_name} /></div><p><b>Full Name: </b> {res.id==localStorage.getItem("this_user_id") ? "you" : res.first_name}</p><p><b>Mobile: </b> {res.mobile}</p><p><b>Location: </b> {res.location}</p><div hidden={(res.is_connected==true) ? false : true} style={{padding: "10px 20px", border: "1px solid #a3dd83", margin: "10px 0px", borderRadius: "50px", width: "fit-content"}}>Connected</div></div>, document.getElementById("search_id_profile"))
            }
        })
        .catch(error => console.log('error', error));
}

function setSearch(text) {
    if (text != null && text != "") {
        var myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        const search_html = (r) => {
            return (<div className="privious" onClick={() => showSearchedProfile(r.id)}><div className="usrimg" style={{ backgroundImage: "url(https://api.coihub.co" + r.profile_image + ")" }}></div><p>{r.id==localStorage.getItem("this_user_id") ? "you" : r.first_name + " " + r.last_name}</p></div>);
        };
        fetch("https://api.coihub.co/search?text=" + text, requestOptions)
            .then(response => response.json())
            .then((res) => {
                ReactDOM.render(res.User.map(search_html), document.getElementById("search_res"));
            })
            .catch(error => console.log('error', error));
    } else {
        ReactDOM.render("", document.getElementById("search_res"));
    }
};

function Nav() {
    const [login_bool, logout_section] = useState(true);
    const [style_messages, showhidemessages] = useState({backgroundColor: "black"});
    const loginshow = () => {
        document.getElementById("loginpage").style.display = "grid";
    }

    const signupshow = () => {
        document.getElementById("signuppage").style.display = "grid";
    }

    function showmessagebar() {
        document.getElementById("msgbox").style.right = "-450px";
        document.getElementById("msgscreen").style.display = "grid";
        showhidemessages({backgroundColor: "#1c294254"});
    }

    const showmessagebox = () => {
        document.getElementById("msgbox").style.right = "0px";
        showhidemessages({backgroundColor: "rgba(10,10,10,0.5)"});
        
    }

    const hidemessagebox = () => {
        document.getElementById("msgbox").style.right = "-550px";
        document.getElementById("msgscreen").style.display = "none";
        showhidemessages({backgroundColor: "rgba(200,200,100,0)"});
    }

    useEffect(()=>{
        var myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

        var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
        };

        fetch("https://api.coihub.co/api/accounts/profile/", requestOptions)
        .then(response => response.json())
        .then(result => localStorage.setItem("this_user_id", result.id))
        .catch(error => console.log('error', error));
    }, []);

    return (
        <>
            <div id="bfologin">
                <BrowserRouter className="main">
                    <div className="navigation" id="main_nav_1" style={{backgroundColor: "transparent"}}>
                        <img className="logotop" src={logo} alt="loding: logo" />
                        <div className="navlinks">
                            <Link className="Link" to="/home">Home</Link>
                            <Link className="Link" id="link2">Co-founder</Link>
                            <Link className="Link" >Investor</Link>
                            <Link className="Link" id="loginbtn" onClick={loginshow}>Login</Link>
                            <Link className="Link" to=""><button onClick={signupshow}>Sign up</button></Link>
                            {/* <Link className="Link" to="home"><img className="web" src={web} /></Link> */}
                        </div>
                    </div>
                    <Routes>
                        <Route path="home" element={<Home></Home>}></Route>
                        <Route path="home.main" element={<Home></Home>}></Route>
                        <Route path="/home" element={<Navigate to={"home.main"}></Navigate>}></Route>
                        <Route path="/" element={<Navigate to={"home"}></Navigate>}></Route>
                        <Route path="/home/1/cofounder" element={<Navigate to="/home.main" />}></Route>
                        <Route path="/home/1/investor" element={<Navigate to="/home.main" />}></Route>
                        <Route path="/home/1/cofounder" element={<Navigate to="/home" />}></Route>
                        <Route path="/home/1/investor" element={<Navigate to="/home" />}></Route>
                        <Route path="/home/investor" element={<Navigate to={"/home.main"}></Navigate>}></Route>
                        <Route path="/home/cofounder" element={<Navigate to={"/home.main"}></Navigate>}></Route>
                        <Route path="/home.main" element={<Navigate to={"/home.main"}></Navigate>}></Route>
                    </Routes>
                </BrowserRouter>
                <Login></Login>
                <Signup></Signup>
            </div>

            <div id="aftlogin" style={{background: "gray"}}>
                <BrowserRouter className="main">
                    <div className="navigation" id="main_nav" style={{backgroundColor: "transparent"}}>
                        <img className="logotop" src={logo} alt="loding: logo" />
                        <div className="navlinks" >
                            <Link className="Link" to="/home/cofounder" id="link2">Co-founder</Link>
                            <Link className="Link" to="/home/investor" id="link3">Investor</Link>
                            <div className="search">
                                <div className="searchopt">
                                    <input type="search" placeholder="Search..." onInput={(e) => setSearch(e.target.value)} />
                                    <div id="search_res"></div>
                                </div>
                            </div>
                            <div className="message" onClick={()=>showmessagebar()}></div>
                            <div className="user" style={{backgroundImage: "url(https://api.coihub.co" + localStorage.getItem("profile_image")}} onMouseEnter={()=>logout_section(false)}></div>
                            <div className="user_logout" id="login_box" hidden={login_bool} onMouseLeave={()=>logout_section(true)}>
                                <div onClick={()=>logout()}><button>Logout</button></div>
                            </div>
                            {/* <Link className="Link" to="home"><img className="web" src={web} /></Link> */}
                        </div>
                    </div>

                    <div className="rightmessagebar" id="msgscreen" style={{backgroundColor: style_messages.backgroundColor}}>
                        <div className="messagebox" id="msgbox">
                            <div className="left">
                                <div className="usr" onClick={showmessagebox}></div>
                                <div className="usr"></div>
                                <div className="usr"></div>
                            </div>
                            <div className="right">
                                <div className="title">New Message<div className="cross" onClick={hidemessagebox}>X</div></div>
                                <div className="prfo">
                                    <div className="name">Ben Walter</div>
                                    <div className="workin">Mircosoft Jaipur</div>
                                </div>
                                <div className="dpandinfo">
                                    <div className="usrdp"></div>
                                    <div className="namedp">Ben Walter</div>
                                    <div className="workindp">Microsoft - Vellore, India</div>
                                    <div className="addressdp">Jaipur, Rajasthan, India</div>
                                </div>
                                <div className="time">
                                    <div className="line2"></div>
                                    <div className="day">Monday</div>
                                    <div className="msg">
                                        <div className="l">
                                            <div className="dpofsender"></div>
                                        </div>
                                        <div className="r">
                                            <div className="nameofsender">
                                                Ben <div className="time">9:39 AM</div>
                                            </div>
                                            <div className="msgs">
                                                <div className="msga">Hi Sofia,</div>
                                                <div className="msga">best Regards, Sofia</div>
                                            </div>
                                        </div>
                                    </div>
                                    <br />
                                    <div className="msg">
                                        <div className="l">
                                            <div className="dpofsender"></div>
                                        </div>
                                        <div className="r">
                                            <div className="nameofsender">
                                                Sofia <div className="time">9:41 AM</div>
                                            </div>
                                            <div className="msgs">
                                                <div className="msga">Hi Ben Would Love to connect with you.</div>
                                                <div className="msga">best Regards, Daniel</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="messagesend"><img src={clip} width="30px" /><input type="text" placeholder="Message" /><img src={send} width="20px" /></div>
                            </div>
                        </div>
                    </div>
                    <div id="search_id_profile"></div>
                    <Routes>
                        <Route path="/home/cofounder" element={<Cofounder></Cofounder>}></Route>
                        <Route path="/home/investor" element={<Investor></Investor>}></Route>
                        <Route path="/home/1/cofounder" element={<Cofounder></Cofounder>}></Route>
                        <Route path="/home/1/investor" element={<Investor></Investor>}></Route> 
                        <Route path="/home.main" element={<Navigate to={"/home/1/cofounder"}></Navigate>}></Route>
                        
                        <Route path="/home" element={<Navigate to={"/home/cofounder"}></Navigate>}></Route>
                        <Route path="/home.main" element={<Navigate to={"/home/cofounder"}></Navigate>}></Route> 
                    </Routes>
                </BrowserRouter>
            </div>
        </>
    )
};

window.onscroll = ()=>{
    if(window.scrollY <= 100) {
        document.getElementById("main_nav").style.backgroundColor = "rgba(0, 0, 50, 0)";
        document.getElementById("main_nav").style.outline = "0px solid rgba(0, 0, 50, 1)";
        document.getElementById("main_nav_1").style.outline = "0px solid black";
    } else {
        document.getElementById("main_nav").style.backgroundColor = "rgba(255, 255, 255, 0)";
        document.getElementById("main_nav").style.outline = "0px solid rgba(0, 0, 50, 1)";
        document.getElementById("main_nav_1").style.backgroundColor = "rgba(255, 255, 255, 0)";
        document.getElementById("main_nav_1").style.outline = "0px solid rgba(0, 0, 50, 1)";
    }

    if(window.scrollY >=800) {
        document.getElementById("main_nav_1").style.backgroundColor = "rgba(0, 0, 50, 1)";
    } 

    if(window.scrollY >=300) {
        document.getElementById("main_nav").style.backgroundColor = "#050505";
    } 
}
export default Nav;
