import React, { useState, useEffect } from "react";
import "./login.scss";

function auto_login() {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };

    fetch("https://api.coihub.co/api/accounts/profile/", requestOptions)
        .then(response => response.json())
        .then((result) => {
            if(result.id) {
                document.getElementById("aftlogin").style.display = "grid";
                document.getElementById("bfologin").style.display = "none";
                localStorage.setItem("profile_image", result.profile_image);
                localStorage.setItem("login_condition", true);
            } else {
                document.getElementById("aftlogin").style.display = "none";
                document.getElementById("bfologin").style.display = "grid";
                localStorage.setItem("login_condition", false);
            }
        })
        .catch(error => console.log('error', error));
}

setTimeout(auto_login(), 100);

function Login() {
    const [number, setNumber] = useState("");
    const [email, setEmail] = useState("");
    const [pass, setPass] = useState("");
    const [otp, setOtp] = useState("");
    const [boolTr, setBoolTr] = useState("translateY(200px)");
    const [boolDis, setBoolDis] = useState(false);
    const [boolDiss, setBoolDiss] = useState(false);

    function anim_up_mob() {
        setBoolTr("translateY(130px)");
        document.getElementById("btn_1").style.transform = "translateY(5px)";
        document.getElementById("mob_1").style.transform = "translateY(5px)";
        document.getElementById("btn_2").style.transform = "translateY(5px)";
        document.getElementById("mob_2").style.transform = "translateY(5px)";
        document.getElementById("eme").style.transform = "translateY(20px) scaleX(70%) scaleY(70%)";
        document.getElementById("pas").style.transform = "translateY(3px) scaleX(70%) scaleY(70%)";
        document.getElementById("log_2").style.transform = "translateY(-15px) scaleX(70%) scaleY(70%)";
        document.getElementById("eme").style.opacity = "0";
        document.getElementById("pas").style.opacity = "0";
        document.getElementById("log_2").style.opacity = "0";
        document.getElementById("btn_1").style.opacity = "1"; 
        document.getElementById("mob_1").style.opacity = "1";
        document.getElementById("btn_2").style.opacity = "1";
        document.getElementById("mob_2").style.opacity = "1";
        document.getElementById("loginpage").style.height = "180px";
        setBoolDis(true);
        setBoolDiss(false);
    }

    useEffect(()=>{
        anim_up_mob();
    }, []);
    
    function anim_up_email() {
        setBoolTr("translateY(180px)");
        document.getElementById("mob_1").style.transform = "translateY(165px) scaleX(70%) scaleY(70%)";
        document.getElementById("btn_1").style.transform = "translateY(147px) scaleX(70%) scaleY(70%)";
        document.getElementById("mob_2").style.transform = "translateY(165px) scaleX(70%) scaleY(70%)";
        document.getElementById("btn_2").style.transform = "translateY(147px) scaleX(70%) scaleY(70%)";
        document.getElementById("pas").style.transform = "translateY(-95px)";
        document.getElementById("log_2").style.transform = "translateY(-95px)";
        document.getElementById("eme").style.transform = "translateY(-95px)";
        document.getElementById("eme").style.opacity = "1";
        document.getElementById("pas").style.opacity = "1";
        document.getElementById("log_2").style.opacity = "1";
        document.getElementById("btn_1").style.opacity = "0"; 
        document.getElementById("mob_1").style.opacity = "0";
        document.getElementById("btn_2").style.opacity = "0";
        document.getElementById("mob_2").style.opacity = "0";
        document.getElementById("loginpage").style.height = "220px";
        setBoolDis(false);
        setBoolDiss(true);
    }

    function close_login() {
        document.getElementById("loginpage").style.display = "none";
    }
    const verify_opt_mob = () => {
        document.getElementById("mob_1").style.display = "none";
        document.getElementById("btn_1").style.display = "none";
        document.getElementById("mob_2").style.display = "none";
        document.getElementById("btn_2").style.display = "none";

        localStorage.setItem("number", number);

        var formdata = new FormData();
        formdata.append("ph_no", number);
        formdata.append("used_for", "login");

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch("https://api.coihub.co/api/accounts/validate-phoneno/", requestOptions)
            .then(response => response.json())
            .then(result => {
                if (result.status == false) {
                    alert(result.detail);
                    document.getElementById("mob_1").style.display = "flex";
                    document.getElementById("mob_2").style.display = "none";
                    document.getElementById("btn_1").style.display = "flex";
                    document.getElementById("btn_2").style.display = "none";
                } else {
                    alert(result.message);
                    document.getElementById("mob_1").style.display = "none";
                    document.getElementById("mob_2").style.display = "flex";
                    document.getElementById("btn_1").style.display = "none";
                    document.getElementById("btn_2").style.display = "flex";
                    localStorage.setItem("login_condition", true);
                }
            })
            .catch(error => console.log('error', error));
    }

    const otp_veri_mobile = () => {
        var formdata = new FormData();
        formdata.append("ph_no", number);
        formdata.append("used_for", "login");
        formdata.append("otp_sent", otp);

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch("https://api.coihub.co/api/accounts/validate-otp-phoneno/", requestOptions)
            .then((e) => e.json())
            .then((r) => {
            const m = r;
            if (m.status == false) {
                alert(m.detail);
            }

            if (m.msg) {
                alert(m.msg);
                localStorage.setItem("token_access", m.token.access);
                localStorage.setItem("token_refresh",m.token.refresh);
                document.getElementById("loginpage").hidden = "none";
                document.getElementById("aftlogin").style.display = "grid";
                document.getElementById("bfologin").style.display = "none";
                window.location.reload();
            }
        });
    }


    const simpleLogin = () => {

        document.getElementById("loginpage").style.display = "none";

        var formdata = new FormData();
        formdata.append("email", email);
        formdata.append("password", pass);

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch("https://api.coihub.co/api/accounts/login-email/", requestOptions).then((e) => e.json()).then((r) => {
            const m = r;
            if (m.errors) {
                alert(m.errors.non_field_errors);
            }

            if (m.msg) {
                alert(m.msg);
                localStorage.setItem("token_access", m.token.access);
                document.getElementById("loginpage").style.display = "none";
                document.getElementById("aftlogin").style.display = "grid";
                document.getElementById("bfologin").style.display = "none";
                localStorage.setItem("login_condition", true);
                window.location.reload();
            }
        })

    }

    return (
        <div className="loginsmall" id="loginpage">
            <p className="cross" onClick={()=>close_login()}>X</p>
            <br />
            <input disabled={boolDiss} type="rel" placeholder="Enter Mobile Number" onChange={(e) => setNumber(e.target.value)} id="mob_1" />
            <input disabled={boolDiss} type="rel" placeholder="Enter OTP" onChange={(e) => setOtp(e.target.value)} id="mob_2" />
            <button disabled={boolDiss} className="button" onClick={verify_opt_mob} id="btn_1">Get OTP</button>
            <button disabled={boolDiss} className="button" onClick={otp_veri_mobile} id="btn_2">Login</button>
            <a style={{position: "fixed", zIndex: "140", transform: boolTr,textAlign: "center", color: "rgb(50,50,200)"}}>or<p hidden={boolDiss} style={{color: "blue", cursor:"pointer"}} onClick={()=>anim_up_email(document.getElementById("eme"))}>with Email</p><p hidden={boolDis} onClick={()=>anim_up_mob(document.getElementById("mob_1"))} style={{color: "blue", cursor:"pointer"}}>with Mobile Number</p></a>
            <input disabled={boolDis} id="eme" type="email" placeholder="Email" onChange={(e) => setEmail(e.target.value)} />
            <input disabled={boolDis} id="pas" type="password" placeholder="Password" onChange={(e) => setPass(e.target.value)} />
            <button disabled={boolDis} id="log_2" className="button" onClick={simpleLogin}>Login</button>
            <div id="showLoginRes"></div>
        </div>
    );
}

export default Login;