window.onload = ()=> { 
        var requestOptions = {
        method: 'GET',
        redirect: 'follow'
    };
  
    fetch("https://api.coihub.co/api/accounts/all_profile/", requestOptions)
        .then(response => response.text())
        .then(result => {
            localStorage.setItem("f_page_json", result);
        })
        .catch(error => console.log('error', error));
}

