import React from "react";
import "../pages.scss";

import img11 from "../image/image11.svg";
import img22 from "../image/image22.svg";
import img33 from "../image/image33.svg";

import connect from "../../ximg/connect.svg";
import location from "../../ximg/location.svg";
import idea from "../../ximg/idea.svg";

import Footer from "../../footerpg";

import ReactDOM from "react-dom";

import "./investor.scss";

import {useState} from "react";

function cardsLoad() {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));
    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };
     
    fetch("https://api.coihub.co/api/accounts/all_profile/auth/", requestOptions)
        .then(response => response.json())
        .then((res) => {
            ReactDOM.render(res.map(cardrender), document.getElementById("investor_cards_box"));
        })
        .catch(error => console.log('error', error));
};

function setPro() {
    document.getElementById("root_2").style.display = "none";
    document.getElementById("investor_cards_box").style.display = "flex";
    ReactDOM.render(null, document.getElementById("root_2"));
}

function request(id_for_req, reqName) {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };

    fetch("https://api.coihub.co/notification/send_connection_request/" + id_for_req, requestOptions)
        .then(response => response.json())
        .then((result) => {
            if(result.status == true) {
                alert("Sended Request to " + reqName);
            } else {
                alert("Unable to send Request to ");
            }
        })
        .catch(error => console.log('error', error));
}

function profile(id_user) {
    document.getElementById("loading_icon").style.display = "flex";
    document.getElementById("root_2").style.display = "grid";
    document.getElementById("investor_cards_box").style.display="none";
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };

    const usersJSON = localStorage.getItem("users");
    const usersJSON_P = JSON.parse(usersJSON);

    const dataHtml = (e)=>{
        if(e.is_invester==true && e.id!=localStorage.getItem("this_user_id")) {
            return(
                <div className={"filterUser"} id={e.first_name} style={{display: "flex", gap: "10px", padding: "10px", borderBottom: "1px solid #F1F1F1"}}>
                    <img src={"https://api.coihub.co" + e.profile_image} style={{width: "50px", height: "50px", borderRadius: "50%"}}></img>
                    <div style={{display: "grid", gridAutoFlow:"column", outline: "0px solid black", width: "100%"}}>
                        <div style={{display: "grid", gap: "5px", justifySelf: "left"}}>
                            <b>{e.first_name + " " + e.last_name}</b>
                            <p>{e.have_skills}</p>
                        </div>
                        <button style={{width: "100px", padding: "5px", margin: "5px 0px", justifySelf: "right"}} onClick={()=>
                            {
                                if(e.is_requested == false) {
                                    request(e.id, e.first_name + " " + e.last_name);
                                }
                            }
                        }>{e.is_connected==true ? "Connected" : e.is_requested==true ? "Requested" : "Connect"}</button>
                    </div>
                </div>
            )
        }
    }

    fetch("https://api.coihub.co/api/accounts/profile/" + id_user + "/", requestOptions)
        .then(response => response.json())
        .then((res) => {
            const elements = (
                <div className="profile_main">
                    <div className="profile_left">
                    <button className="backButton" onClick={()=>setPro()}>Back</button>
                        <div className="section">
                            <div className="profile_images">
                                <img src={"https://api.coihub.co" + res.cover_image} className="cover_image" />
                                <img src={"https://api.coihub.co" + res.profile_image} className="profile_image" />
                            </div>
                            <div>
                                <h3>{res.first_name}</h3>
                                <div>{res.location}</div>
                            </div>
                            <div className="button_cont">
                                <button value={res.id} style={{backgroundColor: "#1C2942", color: "white"}}>Message</button>
                                <button style={{position: "relative", textAlign: "center", padding: "10px 40px"}} onMouseOver={()=>{
                                        document.getElementById("visibleMsg").style.display = "grid";
                                    }}><b>More</b>
                                <div style={{display: "none", borderRadius: "10px", position: "absolute", backgroundColor: "white", padding: "40px", width: "300px", boxShadow: "0px 0px 5px #2B2B2B", gap: "20px"}} id="visibleMsg" onMouseLeave={()=>{
                                   document.getElementById("visibleMsg").style.display = "none"; 
                                }}>
                                        <div className="simpleText" style={{display: "flex", gap: "10px", color: "#666666"}}><img src={img11} width={"20px"}></img>Share Profile</div>
                                        <div className="simpleText" style={{display: "flex", gap: "10px", color: "#666666"}}><img src={img22} width={"20px"}></img>Remove Connection</div>
                                        <div className="simpleText" style={{display: "flex", gap: "10px", color: "#666666"}}><img src={img33} width={"20px"}></img>Block account</div>
                                    </div>
                                </button>
                            </div>
                            <div className="skills_section">
                                <h3>Skills</h3>
                                <div>{res.have_skills}</div>
                                <div className="line"></div>
                                <h3>Looking for Skills</h3>
                                <div>{res.looking_for_skills}</div>
                            </div>
                        </div>
                        <div className="section">
                            <h3>Experience</h3>
                        </div>
                        <div className="section">
                            <h3>Why Partner with me</h3>
                        </div>
                    </div>
                    <div className="profile_right">
                        <div className="section">
                            <div className="google_switch">
                                <img src={"https://api.coihub.co" + res.profile_image} width="50px" height="50px" />
                                <img src="https://banner2.cleanpng.com/20180324/iww/kisspng-google-logo-g-suite-google-5ab6f1cee66464.5739288415219388949437.jpg" width="50px" height="50px" />
                            </div>
                            <p>John, explore co-founders that match your skills. You are logged in with your <b>Google</b> account</p>
                            <button><b>Change or Delete</b></button>
                        </div>
                        <div className="section">
                            <b>Investor</b>
                            <div className="search">
                                <input type="text" placeholder="Search co-founders..." onInput={(e)=>{
                                    let fu = document.querySelectorAll(".filterUser");
                                    fu.forEach((ele)=>{
                                        if(e.target.value[0]=="" || e.target.value[0]==null) {
                                            ele.style.display = "flex";
                                        } else {
                                            let name = ele.id.toLowerCase();
                                            if(name.includes(e.target.value.toLowerCase())) {
                                                ele.style.display = "flex";
                                            } else {
                                                ele.style.display = "none";
                                            }
                                        }
                                    })}}/>
                                <div className="profiles_search" id="profiles_search_area">
                                    {usersJSON_P.map(dataHtml)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
            ReactDOM.render(elements, document.getElementById("root_2"));
            document.getElementById("loading_icon").style.display = "none";
        })
        .catch(error => console.log('error', error));
}
const cardrender = (e) => {
    const this_user = localStorage.getItem("this_user_id");
    if(e.is_invester==true) {
        if(this_user != e.id) {
            return (
                <div className="cofoundercard" style={{backgroundImage: "url(https://api.coihub.co" + e.cover_image + ")", width: "400px", height: "300px", borderTop: "1px solid rgb(200,200,200)"}}>
                    <div className="cocardtop">
                        <div className="designerbtn">
                            {e.profile_headline}
                        </div>
                        <div className="connect">
                            <img src={connect}></img>
                            <div style={{cursor: "pointer"}} className="connectbtn" id={e.id} onClick={(d) => {
                                if(e.is_requested != true) {
                                        request(d.target.id, e.first_name + " " + e.last_name);
                                    }
                                }
                            }>{e.is_connected==true ? "Connected" : e.is_requested==true ? "Requested" : "Connect"}</div>
                        </div>
                    </div>
                    <div className="cofounderbottom">
                        <div className="cofounderdetails">
                            <img className="cofounderdp" src={"https://api.coihub.co" + e.profile_image}></img>
                            <div className="cofoundername">
                                {e.first_name + " " + e.last_name}
                                <div className="underline"></div>
                            </div>
                        </div>
                        <br />
                        <div className="locationandidea">
                            <div className="location">
                                <img src={location} />
                                {e.location + ", " + e.city}
                            </div> <div className="idea">
                                <img src={idea} />
                                {e.idea_business_type}
                            </div>
                        </div>
                        <div className="cofounderidea">
                            <div className="cofounderideahead">
                                {e.first_name} idea:</div>
                            <div className="cofounderideasub">{e.bio}</div>
                        </div>
                        <button className="cofounderideabutton" value={e.id} onClick={(m)=>profile(m.target.value)}>View Profile</button>
                    </div>
                </div>
            )
        }
    }
}

const showInter = (xye) => {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };

    fetch("https://api.coihub.co/api/accounts/get-interest/filter/" + xye, requestOptions)
        .then(response => response.json())
        .then((res) => {
            ReactDOM.render(res.data.map(cardrender), document.getElementById("investor_cards_box"));
        })
        .catch(error => console.log('error', error));
}

const btnCol = (idd)=> {
    const btn = document.querySelectorAll(".btnuns");
    btn.forEach((e)=>{
        e.style.backgroundColor = "white";
        e.style.color = "";
    });
    document.getElementById(idd.target.id).style.backgroundColor = "black";
    document.getElementById(idd.target.id).style.color = "white";  
}

const interEle = (k) => {
    return (
        <div className="btnuns" id={k.id} onClick={(sk) => {showInter(sk.target.id + "/"); btnCol(sk)}}>{k.name}</div>
    )
}

function checkIntr() {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + localStorage.getItem("token_access"));

    var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
    };

    fetch("https://api.coihub.co/api/accounts/get-interest/", requestOptions)
        .then(response => response.json())
        .then((result) => {
            ReactDOM.render(result.map(interEle), document.getElementById("intrests"));
        })
        .catch(error => console.log('error', error));
}

const getInterData = () => {
    checkIntr();
};


function loadall() {
    showInter(""); 
}


function Investor() {
    return (
        <>
            <div className="cofounderpg" onLoad={cardsLoad()}>
                <div className="topbackground" style={{height: "200px"}}>
                    <button>INVESTOR</button>
                    <div className="head">An investor is an individual that puts money into an entity such as a business for a financial return.</div>
                    <div className="sub">Our intuitive messaging system provides quick and easy communication amongst other co-founders and our filters allow you to easily narrow down the type of co-founder you're after.</div>
                </div>
                <div className="scrollablelinks" onLoad={getInterData()}>
                    <div className="btnuns" id="1st" style={{backgroundColor: "black"}} onClick={(t)=>{loadall(); btnCol(t)}} >All</div>
                    <div id="intrests"></div>
                </div>
                <div className="cofounder" style={{padding: "3em"}}>
                    <div id="root_2"></div>
                    <img src="https://acegif.com/wp-content/uploads/loading-58.gif" className="loading" id="loading_icon" />
                    <div className="cofoundercardbox" id="investor_cards_box">
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </>
    );
}

export default Investor;